import React, { useRef, useState } from 'react'
import styled from 'styled-components';
import BackgroundImage from '../../components/BackgroundImage';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame'
import backgroundImg from './assets/home-code-bg.png';
import lockIconImg from './assets/code-lock-icon.png';
import sendIconImg from './assets/code-send-icon.png';
import gameButtonBgImg from './assets/home-stage-btn-background.png';
import gameButtonBgLockImg from './assets/home-stage-btn-background-lock.png';
import stage1IconImg from './assets/home-stage1-icon.png';
import stage2IconImg from './assets/home-stage2-icon.png';
import stage3IconImg from './assets/home-stage3-icon.png';
import stageTitle1IconImg from './assets/home-stage-1-red.png';
import stageTitle2IconImg from './assets/home-stage2.png';
import stageTitle3IconImg from './assets/home-stage3-red.png';
import stageTitle2DisabledIconImg from './assets/home-stage2-gray.png';
import stageTitle3DisabledIconImg from './assets/home-stage3-gray.png';
import stageLockIcon from './assets/home-stage-lock-icon.png';
import codeWrongImg from './assets/code-wrong-text.png';

const stages = [
    {   
        name: 'stage1',
        icon: stage1IconImg,
        titleIcon: stageTitle1IconImg,
        stageName: '숨겨진 물건',
        path: '/stage1-description'
    },
    {
        name: 'stage2',
        icon: stage2IconImg,
        titleIcon: stageTitle2IconImg,
        disabledIcon: stageTitle2DisabledIconImg,
        stageName: '가려진 자, 파헤치는 자',
        path: '/stage2-1'
    },
    {
        name: 'stage3',
        icon: stage3IconImg,
        titleIcon: stageTitle3IconImg,
        disabledIcon: stageTitle3DisabledIconImg,
        stageName: '오토 - 파일럿',
        path: '/stage3-1'
    },
] as const;

function Intro() {
    const [isCorrectCode, setIsCorrectCode] = useState(true);
    const { state, successEnterCode, goto } = useGame();
    const codeRef = useRef<HTMLInputElement>(null);

    const handleEnterCode = () => {
        const { validity } = codeRef.current as HTMLInputElement;

        if (validity.valid) {
            successEnterCode();
            setIsCorrectCode(true);
        } else {
            setIsCorrectCode(false);
        }
    }

    return (
        <Layout>
            <BackgroundImage src={backgroundImg} alt="intro" />
            {
                state.isSuccessEnterCode ? 
                        <GameWrapper>
                            {
                                stages.map((stage, idx) => {
                                    if (state[stage.name].open) {
                                        return (
                                            <GameButtonWrapper key={stage.stageName}>
                                                <GameTitleIcon src={stage.titleIcon} alt={`stage${idx + 1}`}/>
                                                <GameButton onClick={() => { goto(stage.path)}}>
                                                    <GameIcon src={stage.icon} alt={stage.stageName}/>
                                                    <GameName>
                                                        {stage.stageName}
                                                    </GameName>
                                                </GameButton>
                                            </GameButtonWrapper>
                                        )
                                    } else {
                                        return (
                                            <GameButtonWrapper key={stage.stageName}>
                                                <GameTitleIcon src={(stage as any).disabledIcon || stage.titleIcon} alt={`stage${idx + 1}`}/>
                                                <GameButton disabled className="lock">
                                                    <GameLockIcon src={stageLockIcon} alt={`stage${idx + 1}은 잠김 상태입니다.`}/>
                                                </GameButton>
                                            </GameButtonWrapper>
                                        );
                                    }
                                })
                            }
                        </GameWrapper> 
                    :   
                        <CodeWrapper>
                            <ContentDesc>
                                입장 코드를 입력하세요.
                            </ContentDesc>
                            <CodeContentWrapper>
                                <CodeIconImage src={lockIconImg} alt="lock icon" />
                                <CodeInput 
                                    ref={codeRef}
                                    required 
                                    onKeyDown={({ code }) => {
                                        if (code === 'Enter') {
                                            handleEnterCode();
                                        }
                                    }} 
                                    type="text" 
                                    pattern="1030" 
                                    maxLength={4}
                                />
                                <CodeIconButton type="button" onClick={handleEnterCode}>
                                    <CodeIconImage src={sendIconImg} alt="send icon" />
                                </CodeIconButton>
                            </CodeContentWrapper>
                            {!isCorrectCode && (
                                    <CodeWrongAnswerImgWrapper>
                                        <img src={codeWrongImg} alt=""/>
                                    </CodeWrongAnswerImgWrapper>
                                )
                            }
                        </CodeWrapper>
            }
        </Layout>
    )
}

export default { pathname: '/', component: Intro } as RouteMap;


// code

const CodeWrapper = styled.div`
    position: absolute;
    top: 61%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 74%;
`

const ContentDesc = styled.p`
    color: #fff;
    text-align: center;
    margin-bottom: 33px;
`

const CodeContentWrapper = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    border: 1px solid #fff;
    border-radius: 8vw;
    padding: 1.5vw;
`

const CodeIconImage = styled.img`
    width: 16%;
    height: auto;
`

const CodeIconButton = styled.button`
        width: 16%;
        height: auto;
        
        img {
            display: block;
            width: 100%;
            height: auto;
        }
`

const CodeInput = styled.input`
    background-color: #fff;
    flex: 1;
    margin: 0 5px;
    text-align: center;
    width: 100%;
`

const CodeWrongAnswerImgWrapper = styled.div`
    width: 87%;
    background-color: rgba(255, 255, 255, 0.6);
    margin: 0 auto;
    margin-top: 1.5vw;
    border-radius: 3vw;
    padding: 1vw;

    img {
        display: block;
        width: 95%;
        height: auto;
        margin: 0 auto;
    }
`

// game

const GameWrapper = styled.ul`
    position: absolute;
    top: 52%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 70%;

    li + li {
        margin-top: 10%;
    }

`

const GameButtonWrapper = styled.li`
    position: relative;
    border-radius: 50px;
    padding: 7px;
    border: 1px solid #fff;
`

const GameButton = styled.button`
    display: flex;
    align-items: center;
    padding: 5px;
    border-radius: 50px;
    background-image: url(${gameButtonBgImg});
    background-size: cover;
    text-decoration: none;
    color: #000;
    width: 100%;

    &.lock {
        justify-content: center;
        background-image: url(${gameButtonBgLockImg});
    }
`

const GameTitleIcon = styled.img`
    position: absolute;
    width: 20%;
    height: auto;
    bottom: calc(100% + 1px);
    left: 7%;
`

const GameIcon = styled.img`
    width: 21%;
    height: auto;
`

const GameName = styled.p`
    text-align: center;
    flex: 1;
    font-size: 95%;
    font-weight: bold;
    font-family: yes24;
`

const GameLockIcon = styled.img`
    width: 21%;
    height: auto;
`
