import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import backgroundImg from './assets/1stage-bg.png';
import stageImg from './assets/1stage-wrong.png';
import backBtnImg from './assets/1stage-wrong-btn-back.png';
import { useHistory } from 'react-router';

function Stage_1_wrong() {
    const history = useHistory();

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage1-wrong" />
            <Button onClick={() => { history.goBack() }}>
                <img src={backBtnImg} alt="돌아가기" />
            </Button>
        </Wrapper>
    )
}

export default { 
    pathname: '/stage1-wrong',
    component: Stage_1_wrong 
} as RouteMap;

const Wrapper = styled(Layout)`
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const Button = styled.button`
    img {
        width: 70vw;
        height: auto;
    }
`