import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter } from 'react-router-dom';
import GameStorageContainer from './containers/GameStorage';
import { storage } from './modules';
import Routes from './routes';
import { Normalize, Reset } from './styles';

ReactDOM.render(
  <React.StrictMode>
    <HashRouter basename="/">
      <Reset />
      <Normalize />
      <GameStorageContainer initialData={storage.getItem('game')}>
        <Routes />
      </GameStorageContainer>
    </HashRouter>
  </React.StrictMode>,
  document.getElementById('root')
);