import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/1stage-bg.png';
import stageImg from './assets/1stage1.png';
import hint from './assets/1stage-hint1.png';

function Stage_1_1({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage1');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage1-1" />
            <Answer 
                answers={["항공박물관"]} 
                onSuccess={(answer) => { 
                    game.answer(answer)
                    game.goto('/stage1-1-1');
                }} 
                onFailure={() => { game.goto('/stage1-wrong'); }} 
                onHint={onShowHint} 
            />
        </Wrapper>
    )
}

export default { 
    pathname: '/stage1-1', 
    component: withHintModal(Stage_1_1)(hint)
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    min-height: 100vh;
    padding-bottom: 20vw;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`
