import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame';
import stageImg from './assets/2stage1.png';
import backgroundImg from './assets/2stage-bg.png';

function Stage_2_1() {
    const game = useGame('stage2');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage2-2" />
            <Answer 
                anyAnswer
                placeholder="최대 4글자로 입력해 주세요."
                answers={[]} 
                onSuccess={(answer) => { 
                    game.answer(answer)
                    game.goto('/stage2-2');
                }} 
                onFailure={() => { }}
            />
        </Wrapper>
    )
}

export default { 
    pathname: '/stage2-1', 
    component: Stage_2_1,
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`
