import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import backgroundImg from './assets/2stage-bg.png';
import stageImg from './assets/2stage-wrong.png';
import gotoBtnImg1 from './assets/2stage-wrong-btn-back.png';
import gotoBtnImg1_hover from './assets/2stage-wrong-btn-back-hover.png';
import { useHistory } from 'react-router';

function Stage_2_wrong() {
    const history = useHistory();

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage1-wrong" />
            <Button onClick={() => { history.goBack() }}>
                <img src={gotoBtnImg1} alt="go to stage3-1-2" />
                <img className="hover" src={gotoBtnImg1_hover} alt="go to stage3-1-2" />
            </Button>
        </Wrapper>
    )
}

export default { 
    pathname: '/stage2-wrong',
    component: Stage_2_wrong 
} as RouteMap;

const Wrapper = styled(Layout)`
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const Button = styled.button`
    display: block;
    margin: 0 auto;

    img {
        display: block;
        width: 70vw;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`