import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/2stage-bg.png';
import stageImg from './assets/2stage8.png';
import hint from './assets/2stage-hint7.png';

function Stage_2_8({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage2');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage2-8" />
            <Answer 
                answers={["그냥 나답게 살고 싶다", "그냥나답게살고싶다"]} 
                onSuccess={(answer) => { 
                    game.answer(answer)
                    game.goto('/stage2-9');
                }} 
                onFailure={() => { game.goto('/stage2-wrong'); }} 
                onHint={onShowHint} 
            />
        </Wrapper>
    )
}

export default { 
    pathname: '/stage2-8', 
    component: withHintModal(Stage_2_8)(hint)
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`
