import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import stageImg from './assets/3stage4.png';
import hint from './assets/3stage-hint3.png';
import backgroundImg from './assets/3stage-bg.png';

function Stage_3_4({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage3');
    const { answer } = game.state.stage3.answers.find((answer) => answer.pathname === '/stage3-1-2') as Answer || {}

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage3-4" />
            <Place>
                {(answer || "").split(',')[1]}
            </Place>
            <Answer 
                answers={["해상 구조용 드론", "해상 구조용", "해상 구조", "해상구조"]} 
                onSuccess={(answer) => { 
                    game.answer(answer)
                    game.goto('/stage3-5');
                }} 
                onFailure={() => { game.goto('/stage3-wrong'); }} 
                onHint={onShowHint} 
            />
        </Wrapper>
    )
}

export default { 
    pathname: '/stage3-4', 
    component: withHintModal(Stage_3_4)(hint)
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) no-repeat;
    background-size: cover;
    background-attachment: fixed;
    min-height: 100vh;
    padding-bottom: 20vw;
`

const Place = styled.span`
    position: absolute;
    top: 26.88%;
    left: 45.5%;
    color: rgba(255, 255, 255, 95%);
    font-size: 133%;
    font-family: disital;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`
