import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import PressButton from '../../components/PressButton';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import stageImg from './assets/3stage5.png';
import stageImg2 from './assets/3stage5-1.png';
import hint from './assets/3stage-hint4.png';
import backgroundImg from './assets/3stage-bg.png';
import gotoBtnImg from './assets/3stage5-btn.png';
import gotoBtnImg_hover from './assets/3stage5-btn-hover.png';
import { useRef } from 'react';

let hasVisited3_5 = false;

function Stage_3_5({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage3');
    const contentRef = useRef<HTMLImageElement>(null);

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage3-5" />
            <StyledPressButton 
                timing={0}
                showContent={hasVisited3_5}
                showButton={true}
                onEnd={() => {
                    window.setTimeout(() => {
                        const contentEl = contentRef.current as HTMLImageElement;
                        
                        window.scrollTo({ top: contentEl.offsetTop, left:0, behavior: 'smooth' })
                    }, 10)
                }}
                render={
                    () => {
                        hasVisited3_5 = true;
                        return (
                            <>
                                <StageImage ref={contentRef} src={stageImg2} alt="stage3-5" />
                                <StyledAnswer 
                                    answers={["통합 항공교통 관리시스템", "에비스", "통합항공교통관리시스템", "통합 항공 교통 관리 시스템"]} 
                                    onSuccess={(answer) => { 
                                        game.answer(answer)
                                        game.goto('/stage3-6');
                                    }} 
                                    onFailure={() => { game.goto('/stage3-wrong'); }} 
                                    onHint={onShowHint} 
                                />
                            </>
                        )
                    }
                } 
            >
                <img src={gotoBtnImg} alt="go to stage3-1-2" />
                <img className="hover" src={gotoBtnImg_hover} alt="go to stage3-1-2" />
            </StyledPressButton>
        </Wrapper>
    )
}

export default { 
    pathname: '/stage3-5', 
    component: withHintModal(Stage_3_5)(hint)
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const StyledPressButton = styled(PressButton)`
    display: block;
    margin: 0 auto;

    &::after {
        opacity: 0;
    }
  
    img {
        display: block;
        width: 40vw;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`

const StyledAnswer = styled(Answer)`
    margin-top: 10vw;
`