import React, { useRef } from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import PressButton from '../../components/PressButton';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/2stage-bg.png';
import stageImg from './assets/2stage7.png';
import stageImg2 from './assets/2stage7-1.png';
import hint from './assets/2stage-hint6.png';

let hasVisited2_7 = false;

function Stage_2_7({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage2');
    const contentRef = useRef<HTMLImageElement>(null);

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage2-7" />
            <StyledPressButton 
                timing={1}
                showContent={hasVisited2_7}
                onEnd={() => {
                    window.setTimeout(() => {
                        const contentEl = contentRef.current as HTMLImageElement;
                        
                        window.scrollTo({ top: contentEl.offsetTop, left:0, behavior: 'smooth' })
                    }, 10)
                }}
                render={
                    () => {
                        hasVisited2_7 = true;
                        return (
                            <>
                                <StageImage ref={contentRef} src={stageImg2} alt="stage2-7" />
                                <StyledAnswer 
                                    answers={["246"]} 
                                    onSuccess={(answer) => { 
                                        game.answer(answer)
                                        game.goto('/stage2-8');
                                    }} 
                                    onFailure={() => { game.goto('/stage2-wrong'); }} 
                                    onHint={onShowHint} 
                                />
                            </>
                        )
                    }
                } 
            />
        </Wrapper>
    )
}

export default { 
    pathname: '/stage2-7', 
    component: withHintModal(Stage_2_7)(hint)
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const StyledPressButton = styled(PressButton)`
    position: absolute;
    top: 81.4%;
    left: 50%;
    transform: translateX(-50%);
    width: 35%;
    height: 39vw;
`

const StyledAnswer = styled(Answer)`
    margin-top: 10vw;
`