import { useHistory } from "react-router";
import { GameStage, useGameStorage } from "../containers/GameStorage";


export default function useGame(stage?: GameStage) {
    const [state, handlers] = useGameStorage();
    const history = useHistory();


    const goto = (path: string) => {
        if (!state.previousPaths.includes(path)) {
            handlers.handleGotoPath(path);
        }
        history.push(path);
    }

    const answer = (answer: string) => {
        if (!stage) throw Error('answer를 사용하려면 stage를 명시해줘야 합니다.');

        const { pathname } = history.location;

        handlers.handleAddAnswer(stage, {
            pathname,
            answer,
        })

    }

    const open = (stage: GameStage) => {
        handlers.handleOpenStage(stage);
    }

    return {
        state,
        successEnterCode: handlers.handleSuccessEnterCode,
        goto,
        answer,
        open,
    }
}