import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/description-bg.png';
import stageImg from './assets/description-img.png';
import gotoBtnImg1 from './assets/description-btn-text.png';
import gotoBtnImg1_hover from './assets/description-btn-text-click.png';

function Stage_1_description() {
    const game = useGame('stage1');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage1-1-1" />
            <Button onClick={() => { game.goto('/stage1-1') }}>
                <img src={gotoBtnImg1} alt="go to stage3-1-2" />
                <img className="hover" src={gotoBtnImg1_hover} alt="go to stage3-1-2" />
            </Button>
        </Wrapper>
    )
}

export default { 
    pathname: '/stage1-description', 
    component: Stage_1_description 
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    min-height: 100vh;
    padding-bottom: 20vw;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const Button = styled.button`
    display: block;
    margin: 0 auto;
    border: 1px solid #000;
    border-radius: 5vw;
    width: 70vw;
    padding: 3vw 0;

    img {
        display: block;
        width: 8vw;
        height: auto;
        margin: 0 auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        background-color: #808080;
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`