import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/1stage-bg.png';
import stageImg from './assets/1stage3.png';
import hint from './assets/1stage-hint3.png';

function Stage_1_3({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage1');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage1-3" />
            <Answer 
                answers={["금강호"]} 
                onSuccess={(answer) => { 
                    game.goto('/stage1-4') 
                    game.answer(answer)
                }} 
                onFailure={() => { game.goto('/stage1-wrong'); }} 
                onHint={onShowHint} 
            />
        </Wrapper>
    )
}

export default { 
    pathname: '/stage1-3', 
    component: withHintModal(Stage_1_3)(hint) 
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`
