import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/1stage-bg.png';
import stageImg from './assets/1stage8.png';
import gotoBtnImg1 from './assets/1stage8-selec-btn1.png';
import gotoBtnImg1_hover from './assets/1stage8-select-btn1-hover.png';
import btn2_open from './assets/1stage8-selec-btn2-open.png';
import btn2_lock from './assets/1stage8-selec-btn2-lock.png';
import gotoBtnImg2_hover from './assets/1stage8-select-btn2-hover.png';

function Stage_1_8() {
    const game = useGame('stage1');
    const disabled = game.state.previousPaths.includes('/stage1-8-1');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage1-1-1" />
            <ButtonWrapper>
                <Button onClick={() => { game.goto('/stage1-8-1') }}>
                    <img src={gotoBtnImg1} alt="go to stage3-1-2" />
                    <img className="hover" src={gotoBtnImg1_hover} alt="go to stage3-1-2" />
                </Button>
                <Button disabled={!disabled} onClick={() => { game.goto('/stage1-8-2') }}>
                    <img className="open" src={btn2_open} alt="쪽지를 열어보자" />
                    <img className="open hover" src={gotoBtnImg2_hover} alt="쪽지를 열어보자" />
                    <img className="lock" src={btn2_lock} alt="쪽지를 열어보자" />
                </Button>
            </ButtonWrapper>
        </Wrapper>
    )
}

export default { 
    pathname: '/stage1-8', 
    component: Stage_1_8 
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    min-height: 100vh;
    padding-bottom: 20vw;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const ButtonWrapper = styled.div`
    width: 70vw;
    margin: 0 auto;
    
    button + button {
        margin-top: 5vw;
    }
`

const Button = styled.button`
    width: 100%;
    cursor: pointer;

    &:disabled {
        img.open {
            display: none;
        }
        img.lock {
            display: block;
        }
    }

    &:not(:disabled) {
        img.lock {
            display: none;
        }
    }

    img {
        display: block;
        width: 100%;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:not(:disabled):hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`