import { createContext, useState, useContext, useEffect } from "react";
import { storage } from "../modules";

export type GameStage = 'stage1' | 'stage2' | 'stage3';
export type GameStorageDataTypes = LocalstorageMap['game'];
export type GameStorageHandlerTypes = {
    handleSuccessEnterCode(): void;
    handleAddAnswer(stage: GameStage, answer: { pathname: string, answer: string }): void;
    handleGotoPath(path: string): void;
    handleOpenStage(stage: GameStage): void;
}

export const GameStorageData = createContext<GameStorageDataTypes | null>(null);
export const GameStorageHandler = createContext<GameStorageHandlerTypes | null>(null);

type GameStorageContainerProps = {
    initialData: GameStorageDataTypes;
    children: React.ReactNode;
}

function GameStorageContainer ({ initialData, children }: GameStorageContainerProps) {
    const [isSuccessEnterCode, setIsSuccessEnterCode] = useState<GameStorageDataTypes['isSuccessEnterCode']>(initialData?.isSuccessEnterCode || false);
    const [previousPaths, setPreviousPaths] = useState<GameStorageDataTypes['previousPaths']>(initialData?.previousPaths || ['/']);
    const [stages, setStages] = useState<Pick<GameStorageDataTypes, 'stage1' | 'stage2' | 'stage3'>>({
        stage1: initialData?.stage1 || {
            answers: [],
            open: true,
        }, 
        stage2: initialData?.stage2 || {
            answers: [],
            open: false,
        }, 
        stage3: initialData?.stage3 || {
            answers: [],
            open: false,
        } 
    });

    useEffect(() => {
        storage.setItem('game', {
            isSuccessEnterCode,
            previousPaths,
            ...stages,
        });
    }, [isSuccessEnterCode, previousPaths, stages])

    const gameStorageHandler: GameStorageHandlerTypes = {
        handleSuccessEnterCode: () => {
            setIsSuccessEnterCode(true);
        },
        handleGotoPath: (path) => {
            setPreviousPaths([...previousPaths, path]);
        },
        handleAddAnswer: (stage, answer) => {

            const stageInfo = stages[stage];

            if (stageInfo.answers.some(answerInfo => answerInfo.pathname === answer.pathname)) {
                setStages({
                    ...stages,
                    [stage]: {
                        ...stageInfo,
                        answers: stageInfo.answers.map(answerInfo => answerInfo.pathname === answer.pathname 
                            ? { 
                                ...answerInfo, 
                                ...answer,
                              } 
                            : answerInfo
                        )
                    }
                })
            } else {
                setStages({
                    ...stages,
                    [stage]: {
                        ...stageInfo,
                        answers: [...stageInfo.answers, answer]
                    }
                })
            }
        },
        handleOpenStage: (stage) => {
            setStages({
               ...stages,
               [stage]: {
                   ...stages[stage],
                   open: true,
               } 
            })
        }
    }

    return (
        <GameStorageHandler.Provider value={gameStorageHandler}>
            <GameStorageData.Provider value={{
                isSuccessEnterCode,
                previousPaths,
                ...stages,
            }}>
                {children}
            </GameStorageData.Provider>
        </GameStorageHandler.Provider>
    )
}

export default GameStorageContainer;


export function useGameStorage(): [GameStorageDataTypes, GameStorageHandlerTypes] {
    const storage = useContext(GameStorageData);
    const handler = useContext(GameStorageHandler);

    if (!storage || !handler) throw new Error('GameStorageContainer 내부에 선언되어야 합니다.');

    return [storage, handler];
}