import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import stageImg from './assets/3stage8.png';
import hint from './assets/3stage-hint7.png';
import backgroundImg from './assets/3stage-bg.png';

function Stage_3_8({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage3');
    const { answer } = game.state.stage3.answers.find((answer) => answer.pathname === '/stage3-1-2') as Answer || {}

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage3-8" />
            <Name>
                {(answer || "").split(',')[1]}
            </Name>
            <Answer 
                answers={["파스칼"]} 
                onSuccess={(answer) => { 
                    game.answer(answer)
                    game.goto('/stage3-9');
                }} 
                onFailure={() => { game.goto('/stage3-wrong'); }} 
                onHint={onShowHint} 
            />
        </Wrapper>
    )
}

export default { 
    pathname: '/stage3-8', 
    component: withHintModal(Stage_3_8)(hint)
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) no-repeat;
    background-size: cover;
    background-attachment: fixed;
    min-height: 100vh;
    padding-bottom: 20vw;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const Name = styled.span`
    position: absolute;
    top: 46.12%;
    left: 19%;
    color: rgba(255, 255, 255, 95%);
    font-size: 130%;
    background-color: transparent;
    font-family: bitro;
`