import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/2stage-bg.png';
import stageImg from './assets/2stage2.png';
import gotoBtnImg1 from './assets/2stage2-select-btn1.png';
import gotoBtnImg1_hover from './assets/2stage2-select-btn1-hover.png';
import gotoBtnImg2 from './assets/2stage2-select-btn2.png';
import gotoBtnImg2_hover from './assets/2stage2-select-btn2-hover.png';

function Stage_2_2() {
    const game = useGame('stage2');
    const { answer } = game.state.stage2.answers.find((answer) => answer.pathname === '/stage2-1') as Answer || {}

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage2-2" />
            <Name>
                {answer || ''}
            </Name>
            <ButtonWrapper>
                <Button onClick={() => { game.goto('/stage2-2-1') }}>
                    <img src={gotoBtnImg1} alt="go to stage3-1-2" />
                    <img className="hover" src={gotoBtnImg1_hover} alt="go to stage3-1-2" />
                </Button>
                <Button onClick={() => { game.goto('/stage2-2-2') }}>
                    <img src={gotoBtnImg2} alt="go to stage3-1-2" />
                    <img className="hover" src={gotoBtnImg2_hover} alt="go to stage3-1-2" />
                </Button>
            </ButtonWrapper>
        </Wrapper>
    )
}

export default { 
    pathname: '/stage2-2', 
    component: Stage_2_2 
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`


const Name = styled.span`
    position: absolute;
    top: 87.08%;
    left: 52%;
    color: rgba(255, 255, 255, 95%);
    font-size: 143%;
    background-color: transparent;
    font-family: yes24;
`


const ButtonWrapper = styled.div`
    width: 70vw;
    margin: 0 auto;

    button + button {
        margin-top: 5vw;
    }
`

const Button = styled.button`
    display: block;
    margin: 0 auto;

    &::after {
        opacity: 0;
    }
  
    img {
        display: block;
        width: 100%;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`