import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/1stage-bg.png';
import stageImg from './assets/1stage1-1.png';
import gotoBtnImg1 from './assets/1stage1-1-btn-enter.png';
import gotoBtnImg1_hover from './assets/1stage1-1-btn-enter-hover.png';

function Stage_1_1_1() {
    const game = useGame('stage1');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage1-1-1" />
            <Button onClick={() => { game.goto('/stage1-2') }}>
                <img src={gotoBtnImg1} alt="go to stage3-1-2" />
                <img className="hover" src={gotoBtnImg1_hover} alt="go to stage3-1-2" />
            </Button>
        </Wrapper>
    )
}

export default { 
    pathname: '/stage1-1-1', 
    component: Stage_1_1_1 
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    min-height: 100vh;
    padding-bottom: 20vw;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const Button = styled.button`
    display: block;
    margin: 0 auto;

    img {
        display: block;
        width: 70vw;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`