import React, { useState } from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame';
import stageImg from './assets/3stage1-2.png';
import nameLabel from './assets/3stage1-2-text-name.png';
import placeLabel from './assets/3stage1-2-text-place.png';
import gotoBtnImg from './assets/3stage1-1-btn.png';
import gotoBtnImg_hover from './assets/3stage1-1-btn-hover.png';
import backgroundImg from './assets/3stage-bg.png';

function Stage_3_1_2() {
    const [ name, setName ] = useState('');
    const [ place, setPlace ] = useState('');
    const game = useGame('stage3');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage3-1-2" />
            <ContentWrapper>
                <label className="width-59 center" htmlFor="name">
                    <img src={nameLabel} alt="성함을 입력해 주세요." />
                </label>
                <Input
                    id="name"
                    placeholder="성함을 입력해주세요 (최대 4글자)"
                    className="margin-bottom-10vw"
                    value={name}
                    onChange={(e) => { 
                        setName(e.target.value);
                    }}
                    maxLength={4}
                />
                <label className="width-95 center" htmlFor="place">
                    <img src={placeLabel} alt="당신이 떠나고 싶은 곳은 어디인가요?" />
                </label>
                <Input 
                    id="place"
                    placeholder="여행지는 최대 5글자로 입력해주세요"
                    className="margin-bottom-14vw"
                    maxLength={5}
                    value={place}
                    onChange={(e) => { 
                        setPlace(e.target.value);
                    }} 
                />
                <Button onClick={() => { 
                    game.answer(`${name},${place}`);
                    game.goto(`/stage3-2`);
                }}>
                    <img className="btn-img" src={gotoBtnImg} alt="go to stage3-1-2" />
                    <img className="btn-img hover" src={gotoBtnImg_hover} alt="go to stage3-1-2" />
                </Button>
            </ContentWrapper>
        </Wrapper>
    )
}

export default { 
    pathname: '/stage3-1-2', 
    component: Stage_3_1_2
} as RouteMap;

const Wrapper = styled(Layout)`
    position: relative;
    background: url(${backgroundImg}) no-repeat;
    background-attachment: fixed;
    min-height: 100vh;
    text-align: center;
`

const StageImage = styled.img`
    display: block;
    width: 100%;
    height: auto;
`

const Input = styled.input`
    width: 100%;
    padding: 2vw 0;
    background-color: #fff;
    border-radius: 2vw;
    text-align: center;
`

const Button = styled.button`
    display: block;
    margin: 0 auto;

    & > .btn-img {
        display: block;
        width: 28vw;
        height: auto;
    }

    & > .btn-img.hover {
        display: none;
    }

    &:hover {
        .btn-img:not(.hover) {
            display: none;
        }
        .btn-img.hover {
            display: block;
        }
    }
`

const ContentWrapper = styled.div`
    width: 63%;
    position: absolute;
    top: 62%;
    left: 50%;
    transform: translateX(-50%);

    label {
        display: block;
    }

    img {
        display: block;
        width: 100%;
        height: auto;
        margin-bottom: 2vw;
    }

    & > * {
        &.center {
            margin-left: auto;
            margin-right: auto;
        }
        &.width-59 {
            width: 59%;
        }
        &.width-95 {
            width: 95%;
        }
        &.margin-bottom-10vw {
            margin-bottom: 10vw;
        }
        &.margin-bottom-14vw {
            margin-bottom: 14vw;
        }
    }
`