import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/2stage-bg.png';
import stageImg from './assets/2stage2-1.png';
import hint from './assets/2stage-hint1.png';
import gotoBtnImg1 from './assets/2stage2-1-btn.png';
import gotoBtnImg1_hover from './assets/2stage2-1-btn-hover.png';

function Stage_2_2_1({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage2');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage2-2-1" />
            <Button onClick={() => { game.goto('/stage2-2-2') }}>
                <img src={gotoBtnImg1} alt="go to stage3-1-2" />
                <img className="hover" src={gotoBtnImg1_hover} alt="go to stage3-1-2" />
            </Button>
        </Wrapper>
    )
}

export default { 
    pathname: '/stage2-2-1', 
    component: withHintModal(Stage_2_2_1)(hint)
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const Button = styled.button`
    display: block;
    margin: 0 auto;
    margin-top: 5vw;

    img {
        display: block;
        width: 70vw;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`
