import React, { useState } from 'react'
import { useCallback } from 'react';
import styled from 'styled-components'
import icon from '../assets/images/hint-modal-icon.png';

export type HintModalRenderProps = {
    onShowHint: () => void;
}

const withHintModal = (Component: any) => (hint: string) => (...props: any[]) => {
    const [show, setShow] = useState(false);

    const handleShowHintModal = useCallback(() => {
        setShow(true);
    }, [])

    return (
        <>
            {
                show && (
                    <Dimmed>
                        <Content>
                            <Icon src={icon} alt="hint" />
                            <Hint src={hint} />
                            <CloseButton onClick={() => { setShow(false) }}>창 닫기</CloseButton>
                        </Content>
                    </Dimmed>
                )
            }
            <Component {...props} onShowHint={handleShowHintModal} />
        </> 
    )
}

export default withHintModal

const Dimmed = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 1000;
`

const Content = styled.div`
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 50%;
    left: 50%;
    width: 80%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 35px 20px 15px;
    border-radius: 6px;
`

const Icon = styled.img`
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 12vw;
    height: auto;
`


const Hint = styled.img`
    width: 100%;
    height: auto;
`

const CloseButton = styled.button`
    padding: 2vw 3vw;
    width: 100%;
    border-radius: 10vw;
    border: 1px solid #000;
    font-size: bold;
    margin: 10px 0;

    &:hover {
        background-color: #999;
        color: #fff;
    }
`