import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/2stage-bg.png';
import stageImg from './assets/2stage3.png';
import hint from './assets/2stage-hint2.png';


function Stage_2_3({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage2');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage2-2-1" />
            <Answer 
                answers={["인천국제공항", "인천 국제 공항", "인천 국제공항"]} 
                onSuccess={(answer) => { 
                    game.answer(answer)
                    game.goto('/stage2-4');
                }} 
                onFailure={() => { game.goto('/stage2-wrong'); }} 
                onHint={onShowHint} 
            />
        </Wrapper>
    )
}

export default { 
    pathname: '/stage2-3', 
    component: withHintModal(Stage_2_3)(hint)
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`
