import React from 'react'
import { Switch, useLocation } from 'react-router-dom';
import pages from '../pages';
import { useEffect } from 'react';
import AuthRoute from './AuthRoute';
import stage1_audio from '../pages/stage1/assets/1stage-bgm.mp3';
import stage2_audio from '../pages/stage2/assets/2stage-bgm.mp3';
import stage3_audio from '../pages/stage3/assets/3stage-bgm.mp3';
import styled from 'styled-components';

const audioList = [{
    pathname: 'stage1',
    audio: stage1_audio,
}, {
    pathname: 'stage2',
    audio: stage2_audio,
}, {
    pathname: 'stage3',
    audio: stage3_audio,
}]

function Routes() {
    const location = useLocation();
    const audioInfo = audioList.find(({ pathname }) => location.pathname.includes(pathname));

    useEffect(() => {
        window.scrollTo({ top: 0, left: 0 });
    }, [location]);

    return (
        <>
            <Switch key="slide" location={location}>
                {Object.values(pages).map(page => {
                    return <AuthRoute exact key={`page-${page.pathname}`} path={page.pathname} component={page.component} />  
                })}
            </Switch>
            {audioInfo && <Audio src={audioInfo.audio} autoPlay loop />}
        </>
    )
}

export default Routes

const Audio = styled.audio`
    visibility: hidden;
`