import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import stageImg from './assets/3stage2.png';
import hint from './assets/3stage-hint1.png';
import backgroundImg from './assets/3stage-bg.png';

function Stage_3_2({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage3');
    const { answer } = game.state.stage3.answers.find((answer) => answer.pathname === '/stage3-1-2') as Answer || {}

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage3-2" />
            <Name>
                {(answer || '').split(',')[0]}
            </Name>
            <Answer 
                answers={["수직"]} 
                onSuccess={(answer) => { 
                    game.answer(answer)
                    game.goto('/stage3-2-1');
                }} 
                onFailure={() => { game.goto('/stage3-wrong'); }} 
                onHint={onShowHint} 
            />
        </Wrapper>
    )
}

export default { 
    pathname: '/stage3-2', 
    component: withHintModal(Stage_3_2)(hint)
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) no-repeat;
    background-size: cover;
    background-attachment: fixed;
    min-height: 100vh;
    padding-bottom: 20vw;
`

const Name = styled.span`
    position: absolute;
    top: 24.7%;
    left: 46%;
    color: rgba(255, 255, 255, 95%);
    font-size: 144%;
    background-color: transparent;
    font-family: disital;

`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`
