import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/3stage-bg.png';
import stageImg from './assets/3stage1.png';
import gotoBtnImg1 from './assets/3stage1-btn1.png';
import gotoBtnImg1_hover from './assets/3stage1-btn1-hover.png';
import gotoBtnImg2 from './assets/3stage1-btn2.png';
import gotoBtnImg2_hover from './assets/3stage1-btn2-hover.png';

function Stage_3_1() {
    const game = useGame('stage3');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage3-1" />
            <Button onClick={() => { game.goto('/stage3-1-2') }}>
                <img src={gotoBtnImg1} alt="go to stage3-1-2" />
                <img className="hover" src={gotoBtnImg1_hover} alt="go to stage3-1-2" />
            </Button>
            <Button onClick={() => { game.goto('/stage3-1-1') }}>
                <img src={gotoBtnImg2} alt="go to stage3-1-1" />
                <img className="hover" src={gotoBtnImg2_hover} alt="go to stage3-1-1" />
            </Button>
        </Wrapper>
    )
}

export default { 
    pathname: '/stage3-1', 
    component: Stage_3_1 
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const Button = styled.button`
    display: block;
    margin: 0 auto;

    img {
        display: block;
        width: 70vw;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`