import React, { useState } from 'react'
import { useCallback } from 'react';
import styled from 'styled-components'

export type ModalRenderProps = {
    onShow: (show: boolean) => void;
}

const withModal = (Component: any) => (renderContent: ((props: ModalRenderProps) => React.ReactNode) | React.ReactNode) => (...props: any[]) => {
    const [show, setShow] = useState(false);

    const handleShowModal = useCallback(() => {
        setShow(true);
    }, [])

    return (
        <>
            {
                show && (
                    <Dimmed>
                        <Content>
                            {typeof renderContent === 'function' ? renderContent({ onShow: setShow }) : renderContent}
                        </Content>
                    </Dimmed>
                )
            }
            <Component {...props} onShow={handleShowModal} />
        </> 
    )
}

export default withModal

const Dimmed = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 1000;
`

const Content = styled.div`
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 50%;
    left: 50%;
    width: 80%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 35px 20px 15px;
    border-radius: 6px;
`