import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import stageImg from './assets/3stage-wrong.png';
import { useHistory } from 'react-router';
import backgroundImg from './assets/3stage-bg.png';
import gotoBtnImg from './assets/3stage-wrong-btn-back.png';
import gotoBtnImg_hover from './assets/3stage-wrong-btn-back-hover.png';

function Stage_3_wrong() {
    const history = useHistory();

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage3-wrong" />
            <Button onClick={() => { history.goBack() }}>
                <img src={gotoBtnImg} alt="go to stage3-1-2" />
                <img className="hover" src={gotoBtnImg_hover} alt="go to stage3-1-2" />
            </Button>
        </Wrapper>
    )
}

export default { 
    pathname: '/stage3-wrong',
    component: Stage_3_wrong 
} as RouteMap;

const Wrapper = styled(Layout)`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: url(${backgroundImg}) no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding-bottom: 20vw;
    height: 100vh;
`

const StageImage = styled.img`
    width: 36.5%;
    height: auto;
    margin-bottom: 20vw;
`

const Button = styled.button`
    display: block;
    margin: 0 auto;

    &::after {
        opacity: 0;
    }
  
    img {
        display: block;
        width: 70vw;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`