import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/1stage-bg.png';
import stageImg from './assets/1stage7.png';
import hint from './assets/1stage-hint7.png';

function Stage_1_7({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage1');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage1-7" />
            <Answer 
                answers={["젊은이들이 꿈을 가지면 그 나라는 희망이 있다"]} 
                onSuccess={(answer) => { 
                    game.goto('/stage1-8')
                    game.answer(answer)
                 }} 
                onFailure={() => { game.goto('/stage1-wrong'); }} 
                onHint={onShowHint}
            />
        </Wrapper>
    )
}

export default { 
    pathname: '/stage1-7', 
    component: withHintModal(Stage_1_7)(hint)
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`
