import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame';
import stageImg from './assets/3stage2-1.png';
import backgroundImg from './assets/3stage-bg.png';
// s-진아 추가
import gotoModal from './assets/3stage2-1-btn.png';
import gotoModal_hover from './assets/3stage2-1-btn-hover.png';
// e-진아 추가
import gotoBtnImg2 from './assets/3stage2-1-modal-text.png';
import gotoBtnImg2_hover from './assets/3stage2-1-modal-text-hover.png';
import modalImg from './assets/3stage2-1-modal.png';
import withModal, { ModalRenderProps } from '../../components/withModal';

function Stage_3_2_1({ onShow }: ModalRenderProps) {
    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage3-2-1" />
            <Button style={{
                color: '#fff'
            }} onClick={() => onShow(true)}>
                 <img src={gotoModal} alt="go to modal" />
                 <img className="hover" src={gotoModal_hover} alt="go to modal" />
            </Button>
        </Wrapper>
    )
}

export default { 
    pathname: '/stage3-2-1', 
    component: withModal(Stage_3_2_1)(
        ({ onShow }) => {
            const game = useGame('stage3');

            return (
                <ModalWrapper>
                    <img src={modalImg} alt="버티포트" />
                    <CloseButton onClick={() => {
                        onShow(false);
                        game.goto('/stage3-3');
                    }}>
                        <img src={gotoBtnImg2} alt="go to stage3-1-1" />
                        <img className="hover" src={gotoBtnImg2_hover} alt="go to stage3-1-1" />
                    </CloseButton>
                </ModalWrapper>
            )
        }
    )
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) no-repeat;
    background-size: cover;
    background-attachment: fixed;
    min-height: 100vh;
    padding-bottom: 20vw;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const Button = styled.button`
    display: block;
    margin: 0 auto;

    img {
        display: block;
        width: 70vw;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`

const ModalWrapper = styled.div`
    background-color: #fff;
    border-radius: 42.8px;

    & > img {
        display: block;
        width: 70%;
        height: auto;
        margin: 0 auto;
        margin-bottom: 8%;
    }
`

const CloseButton = styled.button`
    padding: 2vw 3vw;
    width: 100%;
    border-radius: 10vw;
    border: 1px solid #000;
    margin: 10px 0;
    font-size: bold;

    img {
        display: block;
        margin: 0 auto;
        width: 15vw;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        background-color: #999999;
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`