import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/2stage-bg.png';
import stageImg from './assets/2stage9.png';
import gotoBtnImg from './assets/2stage9-btn.png';
import gotoBtnImg_hover from './assets/2stage9-btn-hover.png';


function Stage_2_9() {
    const game = useGame('stage2');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage2-9" />
            <Button onClick={() => { game.goto('/stage2-10') }}>
                <img src={gotoBtnImg} alt="go to stage3-1-2" />
                <img className="hover" src={gotoBtnImg_hover} alt="go to stage3-1-2" />
            </Button>
        </Wrapper>
    )
}

export default { 
    pathname: '/stage2-9', 
    component: Stage_2_9 
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const Button = styled.button`
    display: block;
    margin: 0 auto;

    img {
        display: block;
        width: 70vw;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`
