import React, { useState } from 'react'
import styled from 'styled-components'

type AnswerProps = {
    answers: string[];
    maxLength?: number;
    placeholder?: string;
    anyAnswer?: boolean;
    onSuccess: (answer: string) => void;
    onFailure: () => void;
    onHint?: () => void;
    className?: string;
}

function Answer({ answers, anyAnswer, className, maxLength, placeholder, onSuccess, onHint, onFailure }: AnswerProps) {
    const [value, setValue] = useState('');

    const confirmAnswer = (value: string) => {
        if (anyAnswer || answers.some(answer => answer === value.trim())) {
            onSuccess(value);
        } else if (value === '힌트') {
            onHint?.();
        } else {
            onFailure();
        }
    }

    const handleEnter = (e: React.KeyboardEvent<HTMLInputElement>) => {
        if (e.key === 'Enter') {
            confirmAnswer(value.trim());
        }
    }

    const handleClick = () => {
        confirmAnswer(value.trim())
    }

    const handleChangeAnswer = (e: React.ChangeEvent<HTMLInputElement>) => {
        setValue(e.target.value);
    }

    return (
        <Wrapper className={className}>
            <Input 
                type="text" 
                placeholder={placeholder || "힌트가 필요하다면 '힌트'를 입력해 주세요."}
                value={value} 
                onKeyUp={handleEnter} 
                onChange={handleChangeAnswer} 
                maxLength={maxLength || 999}
            />
            <Button disabled={!value.trim()} onClick={handleClick} />  
        </Wrapper>
    )
}

export default Answer

const Wrapper = styled.div`
    display: flex;
    align-items: stretch;
    width: 80%;
    margin: 0 auto;
`

const Input = styled.input`
    width: 89%;
    border: 1px solid #ccc;
    border-top-left-radius: 10vw;
    border-bottom-left-radius: 10vw;
    padding: 3vw 3.5vw;
    background-color: #fff;
    margin-right: 1%;
    text-align: center;

    &::placeholder {
        color: #999;
    }
`;

const Button = styled.button`
    display: flex;
    align-items: center;
    justify-content: center;
    width: 10%;
    border-top-right-radius: 10vw;
    border-bottom-right-radius: 10vw;
    background-color: #fff;
    border: 1px solid #ccc;

    &::after {
        content: '';
        position: absolute;
        border-top: 1.8vw solid transparent;
        border-right: 2.7vw solid #f7931e;
        border-bottom: 1.8vw solid transparent;
        transform: rotate(-180deg);
    }

    &:disabled::after {
        border-right: 2.7vw solid #999;
    }
`