export function getItem<K extends keyof LocalstorageMap>(key: K) {
    const game = window.localStorage.getItem(key);

    if (!game) return;

    return JSON.parse(game);
}

export function setItem<K extends keyof LocalstorageMap>(key: K, data: LocalstorageMap[K]) {
    return window.localStorage.setItem(key, JSON.stringify(data));
}