import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/2stage-bg.png';
import stageImg from './assets/2stage4.png';
import hint from './assets/2stage-hint3.png';

function Stage_2_4({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage2');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage2-2-1" />
            <Answer 
                answers={["살고 싶으면 무시하지마", "살고 싶으면 무시하지 마", "살고싶으면무시하지마", "살고싶으면 무시하지마", "살고싶으면 무시하지 마"]} 
                onSuccess={(answer) => { 
                    game.answer(answer)
                    game.goto('/stage2-5');
                }} 
                onFailure={() => { game.goto('/stage2-wrong'); }} 
                onHint={onShowHint} 
            />
        </Wrapper>
    )
}

export default { 
    pathname: '/stage2-4', 
    component: withHintModal(Stage_2_4)(hint)
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`
